import React from 'react';
import AppMode from '../AppMode.js';
import FeedInterests from './FeedInterests.js';
import FeedSchool from './FeedSchool.js';

class FeedPage extends React.Component {

    render() {
      switch(this.props.mode) {
        case AppMode.FEED:
          return (
          <div className="padded-page">
              <h1 align="center"><br></br></h1>
              <section>
                <article>
                  <h1>About Me</h1>
                  <p>"The way to get started is to quit talking and begin doing." -Walt Disney</p>
                  <p>"Whoever is happy will make others happy too." -Anne Frank</p>
                  <p>"You have brains in your head. You have feet in your shoes. You can steer yourself any direction you choose." -Dr. Seuss</p>
                </article>
              </section>
          </div>
          );
        break;
        case AppMode.FEED_INTERESTS:
            return (
              <FeedInterests mode={this.props.mode}/>
            );
            break;
        case AppMode.FEED_SCHOOL:
          return (
            <FeedSchool mode={this.props.mode}/>
            );
            break;
      }
        
    }   
}

export default FeedPage;